    numOfPlaceholder: function() {
      var discovered = 0
      var items = this.logicalItems
      for (var key in items) {
        if (items[key].discovered) {
          discovered++
        }
      }
      return 9 - discovered
    },
    discoveredItems: function() {
      var result = []
      var items = this.logicalItems
      for (var key in items) {
        if (items[key].discovered) {
          result.push(items[key])
        }
      }
      return result
    },
