        return ["Rank S", "\"滴滴\"，手机发来悦耳的消息提醒，是雪晴给我发消息了。\"风之诗我练了快两个月了诶，最近感觉老是没什么进步啊\"，我抱怨道，\"诶，你不是也弹古筝吗，你以前是怎么练琴的呀？\"\"在雪晴的耐心分析下我知道自己的基本功好像有所欠缺。回头一看，之前买的指弹书上面已经蒙上了一层薄薄的灰尘，我拿起它，翻到练习曲目，认真地抱起琴练了起来……"]
      } else if (resultList.indexOf(2) < resultList.indexOf(5)) {
        return ["Rank B", "在我手边安静的躺着之前买的指弹书。\"滴滴\"，手机发来悦耳的消息提醒，是雪晴给我发消息了。我蹭的一下坐起来，指弹书掉在了地上。\"风之诗我练了快两个月了诶，最近感觉老是没什么进步啊\"，我抱怨道，\"诶，你不是也弹古筝吗，你以前是怎么练琴的呀？\"在雪晴的耐心分析下我知道自己的基本功好像有所欠缺。回头一看，指弹书却不见踪影。\"我记得我好像有本书来着怎么不见了？\"我挠了挠头。\"算了算了。\"我想道，日复一日的三天打鱼两天晒网，让我的琴技非但没有进步，反而慢慢退化。最后也放弃了练琴。"]
      } else {
        return ["Bad End", "日复一日的三天打鱼两天晒网，让我的琴技非但没有进步，反而慢慢退化。手上的茧渐渐消去，再拿起琴的时候已经觉得苦不堪言。\"唉，难道就到此为止了吗\"，我想道。要是之前有高人指点迷津，好好练琴就好了……"]
      }
    }
  },
  props: {
    msg: String
  },
  methods: {
    closeBox: function() {
      this.displayTextBox = false
    },
    displayGuitarInfo: function(event) {
      this.displayHoverInfo = true
      this.cursorX = event.clientX - 440
      this.cursorY = event.clientY - 130
      this.innerInfo = "guitar"
    },
    displayNotebooktInfo: function(event) {
      this.displayHoverInfo = true
      this.cursorX = event.clientX - 440
      this.cursorY = event.clientY - 130
      this.innerInfo = "manga"
    },
    displayMonitorInfo: function(event) {
      this.displayHoverInfo = true
      this.cursorX = event.clientX - 440
      this.cursorY = event.clientY - 130
      this.innerInfo = "displayer"
    },
    displaySwitchInfo: function(event) {
      this.displayHoverInfo = true
      this.cursorX = event.clientX - 440
      this.cursorY = event.clientY - 130
      this.innerInfo = "ns"
    },
    displayHinataInfo: function(event) {
      this.displayHoverInfo = true
      this.cursorX = event.clientX - 440
      this.cursorY = event.clientY - 130
      this.innerInfo = "hyuga"
    },
    displayStereoInfo: function(event) {
      this.displayHoverInfo = true
      this.cursorX = event.clientX - 440
      this.cursorY = event.clientY - 130
      this.innerInfo = "stereo"
    },
    eraseHoverInfo: function() {
      this.displayHoverInfo = false
    },
    checkItem: function(itemName, eventName) {
      if (itemName == "stereo" && eventName == "listen") {
        var audio = new Audio(require('../assets/idinvalid.mp3'))
        audio.volume = 0.1
        audio.play()
      }
      this.displayTextBox = true
      
      this.displayText = this.scripts.items[itemName][this.scripts.items[itemName].currentState][eventName].message
      this.scripts.items[itemName][this.scripts.items[itemName].currentState][eventName].checked = true
    },
    hideMessage: function() {
      this.displayTextBox = false
    },
    cancelMask: function() {
      this.displayHoverInfo = false
    },
    exitClicked: function() {
      this.showSelectScene = true
      this.showButtons = false
    },
    selectScene: function(sceneNumber) {
      this.currentScene = sceneNumber
      this.showButtons = true
      this.showSelectScene = false
    },
    startInv: function() {
      this.showManu = false
    },
    backToManu: function() {
      this.showManu = true
      this.showButtons = true
      this.showSelectScene = false
      this.displayHoverInfo = false
      this.displayTextBox = false
    },
    toggleItems: function() {
      this.showItems = !this.showItems
    },
    logicalItemHover: function(itemName, description, image) {
      this.itemName = itemName
      this.itemDescription = description
      this.itemImage = image
    },
    resetLogicalItem: function() {
      this.itemName = "啥都没有"
      this.itemDescription = "阿巴阿巴"
      this.itemImage = null
    },
    openTalkMode: function() {
      this.talkMode = true
      this.showManu = false
    },
    closeTalkMode: function(lastWords) {
      if (lastWords != "") {
        this.showItems = false
        this.talkWord = "看来你还没有找到所有的证据呢，等你全找到了再来找我吧！"
        this.talkScript = []
        this.showingEvidence = false
      } else {
        this.talkMode = false
        this.showItems = false
        this.showManu = true
        this.talkScript = [
          "我：你在搞什么名堂？",
          "小杨同学：啊？没啥啊，你可以随便逛一逛看一看！",
          "我：你是不是在偷偷给我准备生日礼物？",
          "小杨同学：我才没...",
          "我：我不信！",
          "小杨同学：那你就拿出证据来啊（坏笑"
        ]
        this.talkWord = "小杨同学：欢迎来到我的房间！"
      }
    },
    talk: function() {
      if (this.talkScript.length <= 0 && !this.switchTold) {
        if (this.talkWord.includes("没有找到所有")) {
          this.closeTalkMode("")
        } else {
          this.showingEvidence = true
          this.showItems = true
          this.show
        }
      } else if (this.talkScript.length <= 0 && this.switchTold) {
        this.talkMode = false
        this.showItems = false
        this.showManu = true
        this.talkScript = []
        this.talkWord = "好嘛好嘛告诉你啦，Switch的密码是201123，去打开看看吧！"
      } else {
        this.talkWord = this.talkScript.shift()
      }
    },
    logicalItemClicked: function(itemId) {
      this.showItems = false
      this.talkWord = this.logicalItems[itemId].confess
      this.logicalItems[itemId].checked = true
      if (this.logicalItems.guitar.checked && this.logicalItems.notebook.checked && this.logicalItems.video.checked) {
        this.talkScript = ["好嘛好嘛告诉你啦，Switch的密码是201123，去打开看看吧！"]
        this.switchTold = true
      } else if (this.logicalItems.guitar.checked && this.logicalItems.notebook.checked && !this.logicalItems.video.checked) {
        this.talkScript = ["不过呢，就算你知道我练了琴，也在给你准备生日礼物，你也没有直接的证据呀！"]
        console.log("guitar notebook passed")
      } else if (this.logicalItems.guitar.checked && !this.logicalItems.notebook.checked && !this.logicalItems.video.checked) {
        this.talkScript = ["但是光靠知道我练琴并不能说明什么的哦！"]
        console.log("guitar passed")
      } else if (!this.logicalItems.guitar.checked && this.logicalItems.notebook.checked && this.logicalItems.video.checked) {
        this.talkScript = ["虽然这个路线有点奇怪不过你还得找到我努力练琴的证据！"]
        console.log("notebook video passed")
      } else if (!this.logicalItems.guitar.checked && this.logicalItems.notebook.checked && !this.logicalItems.video.checked) {
        this.talkScript = ["不过你得告诉我你找到了什么关于\"生日礼物\"礼物本身的线索，我才能给你提示！"]
        console.log("notebook passed")
      } else if (!this.logicalItems.guitar.checked && !this.logicalItems.notebook.checked && this.logicalItems.video.checked) {
        this.talkScript = ["不过你居然一上来就把录像找到了可还行！那你也不知道我要送给谁对吧～"]
        console.log("video passed")
      } else if (this.logicalItems.guitar.checked && !this.logicalItems.notebook.checked && this.logicalItems.video.checked) {
        this.talkScript = ["不过你也不知道我要送给谁对吧～"]
        console.log("guitar video passed")
      }
      this.showingEvidence = false
    },
    onMove({ relatedContext, draggedContext }) {
      const relatedElement = relatedContext.element;
      const draggedElement = draggedContext.element;
      return (
        (!relatedElement || !relatedElement.fixed) && !draggedElement.fixed
      );
    },
    showWillResultFunc: function() {
      if (!this.showWillResult && !this.quiz1Finished) {
        var resultList = []
        for (var i in this.willList) {
          resultList.push(this.willList[i].order)
        }
        if (JSON.stringify(resultList) == "[1,4,5,2,3,6]" || JSON.stringify(resultList) == "[1,5,4,2,3,6]") {
          console.log("quiz 1 solved")
          this.quiz1Finished = true
        }
      }
      this.showWillResult = !this.showWillResult
    },
    closeWill: function() {
      this.showWillGame = false
    },
    closeSteam: function() {
      this.showSteam = false
    },
    openWill: function() {
      this.showWillGame = true
    },
    rollBack: function() {
      this.willList = [
        {
          "name": "在一个夜黑风高的夜晚，结束了一天疲惫的工作，跑完步后的我回到家里。身体还没有凉下来，我不打算先洗澡。“先练练琴吧”，我掏出了吉他。",
          "order": 1,
          "fixed": true
        },
        {
          "name": "指弹书放在一边。",
          "order": 2,
          "fixed": false
        },
        {
          "name": "我抱起琴开始练习。",
          "order": 3,
          "fixed": false
        },
        {
          "name": "练习的曲目是押尾光太郎的「風の詩」，四星的难度对于练琴不久的新手来说着实有些吃力。已经两个月了，我还是没有什么进步。",
          "order": 4,
          "fixed": false
        },
        {
          "name": "雪晴突然发来了消息。",
          "order": 5,
          "fixed": false
        },
        {
          "name": "不知不觉跟雪晴聊了好久。“啊……已经十一点了吗”，我看了看表，“要不还是先睡了吧”。我回到楼上，草草冲了个澡就睡了。",
          "order": 6,
          "fixed": true
        }
      ]
    },
    closeVideo: function() {
      this.showVideo=false
      this.displayTextBox = true
      this.displayText = "已将「かたわれ時」吉他视频纳入法庭记录。"
      this.logicalItems.video.discovered = true
    }
 }
}
